/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
	color:#0c0;
	text-decoration: none;

	&:visited {
		color:  #0c0;;
	}
	&:hover,
	&:focus,
	&:active {
		color:  #0c0;;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

